@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
:root {
    --accent: rgba(0, 255, 204, 1);
    --accenttransparent: rgba(0, 255, 204, 0.25);
    --accentdark: rgba(0, 207, 165, 1);
    --accentdarktransparent: rgba(0, 207, 165, 0.25);
    --accentextradark: rgba(3, 171, 137, 1);
    --accentextradarktransparent: rgba(3, 171, 137, 0.25);
}

body h1 {
    font-family: 'Poppins';
    font-weight: 500;
}

#background {
    transform-origin: top center;
    position: fixed;
    transform: translateX(-50vw) rotate(45deg);
    z-index: -100;
}

#backgroundfiller {
    transform-origin: top center;
    position: fixed;
    background-color: rgba(0, 255, 204, 1);
    background-color: var(--accent);
    height: 1000vh;
    width: 200vw;

    transform: translateX(-50vw) translateY(1vh) rotate(-135deg);
    z-index: -50;
}

#foreground {
    position: relative;
    z-index: 100;
}

body {
    font-family: 'Poppins';
    font-weight: 300;
    background-color: #eee;
    /* background-image: url(https://images.unsplash.com/photo-1519479029449-38c9aed0f322?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1952&q=100); */
    background-size: cover;
    color: rgba(0, 0, 0, .75);
}

#site {
    position: relative;
    min-height: 100vh;
    max-width: 100vw;
    overflow-x: hidden;
}

body h1 {
    position: relative;
    float: left;
    font-size: 3em;
}

body h2 {
    font-size: 2em;
}

body a {
    color: rgba(0, 255, 204, 1);
    color: var(--accent);
}

body a:hover {
    color: rgba(0, 207, 165, 1);
    color: var(--accentdark);
}

body code {
    color: rgba(3, 171, 137, 1);
    color: var(--accentextradark);
    font-family: 'Ubuntu Mono';
    font-size: 1em;
}

body hr {
    width: 10em;
    margin-top: 5em;
    margin-bottom: 5em;
    background-color: darkgrey;
}


body .btn-outline-primary {
    color: rgba(0, 207, 165, 1);
    color: var(--accentdark);
    border-color: rgba(0, 207, 165, 1);
    border-color: var(--accentdark); 
    margin-right: 1em;
    border-radius: 10em;
    border-width: 0.1em;
    font-weight: 500;
    display: inline-block;
}

body .btn-outline-primary:focus {
    color: white;
    border-color: rgba(0, 207, 165, 1);
    border-color: var(--accentdark);
    background-color: rgba(0, 207, 165, 1);
    background-color: var(--accentdark);
    box-shadow: 0 0 0 0.2rem rgba(0, 207, 165, 0.25);
    box-shadow: 0 0 0 0.2rem var(--accentdarktransparent);
}

body .btn-outline-primary:active {
    color: white;
    border-color: rgba(3, 171, 137, 1) !important;
    border-color: var(--accentextradark) !important;
    background-color: rgba(3, 171, 137, 1) !important;
    background-color: var(--accentextradark) !important;
    box-shadow: 0 0 0 0.2rem rgba(3, 171, 137, 0.25) !important;
    box-shadow: 0 0 0 0.2rem var(--accentextradarktransparent) !important;
}

body .btn-outline-primary:hover {
    border-color: rgba(0, 207, 165, 1);
    border-color: var(--accentdark);
    background-color: rgba(0, 207, 165, 1);
    background-color: var(--accentdark);
}

.navbar {
    background-color: rgba(255, 255, 255, 0);
    /* background-color: rgba(33, 33, 33, 1); */
}

body .navbar-brand {
    color: rgba(0, 0, 0, .7) !important;
    font-style: normal;
    font-weight: bold;
}

body .navbar-brand:hover {
    color: rgba(0, 0, 0, 1) !important;
}

body .navbar-nav .nav-link:hover {
    color: rgba(0, 0, 0, 1) !important;
}

body .navbar-nav .nav-link {
    color: rgba(0, 0, 0, .7) !important;
    font-weight: 400;
    font-style: normal;
}
.landing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    max-width: 600px;
    align-items: left;
}

.about {
    font-size: 1.2em;
}

.tag {
    display: inline-block;
    margin-right: 0.25em;
    margin-top: 0.25em;

    padding-top: 0.5em;
    padding-bottom: 0.5em;
    padding-left: 0.75em;
    padding-right: 0.75em;


    border-radius: 100em;
    border: solid;
    border-width: 1px;
    border-color: transparent;

    color: white;
    font-size: 0.9em;
    font-weight: 500;

    background-color: var(--accentdark);

}
.project {
    margin-top: 2em;
    padding-top: 2em;
    padding-bottom: 2em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 5em;
    background-color: #fff;
}

.description {
    display: flex;
    flex-wrap: wrap;
}

.buttons {
    display: flex;
    justify-content: center;
}

.projectmargin {
    margin-bottom: 1rem;
}
.footer {
    font-size: 0.75em;
}
body .bg {
    position: relative;
    z-index: -1;
    display: grid;
    bottom: 0;
    width: 200vw;
    background:var(--accent);
}



.bg .bubbles .bubble {
    position: absolute;
    right:50%;
    right:var(--position, 50%);
    background:var(--accent);
    border-radius:100%;

    -webkit-animation-name: bubble-size, bubble-move;

            animation-name: bubble-size, bubble-move;
    -webkit-animation-duration: 4s, 4s;
            animation-duration: 4s, 4s;
    -webkit-animation-duration: var(--time, 4s), var(--time, 4s);
            animation-duration: var(--time, 4s), var(--time, 4s);
    -webkit-animation-timing-function: ease-in, ease-in;
            animation-timing-function: ease-in, ease-in;
    -webkit-animation-iteration-count: infinite, infinite;
            animation-iteration-count: infinite, infinite;
    -webkit-animation-delay: 0.1s, 0.1s;
            animation-delay: 0.1s, 0.1s;
    -webkit-animation-delay: var(--delay, 0.1s), var(--delay, 0.1s);
            animation-delay: var(--delay, 0.1s), var(--delay, 0.1s);
    -webkit-animation-direction: normal, normal;
            animation-direction: normal, normal;
    -webkit-animation-fill-mode: none, none;
            animation-fill-mode: none, none;
    -webkit-animation-play-state: running, running;
            animation-play-state: running, running;

    /* -webkit-animation-name: bubble-size, bubble-move;
    -webkit-animation-duration: var(--time, 4s), var(--time, 4s);
    -webkit-animation-timing-function: ease-in, ease-in;
    -webkit-animation-iteration-count: infinite, infinite;
    -webkit-animation-delay: var(--delay, 0.1s), var(--delay, 0.1s);
    -webkit-animation-direction: normal, normal;
    -webkit-animation-fill-mode: none, none;
    -webkit-animation-play-state: running, running; */

    /* transform:translate3d(-50%, 100%, 0); */
    transform: translate(-50%, 100%);
}

.bg .bubbles {
    transform: translate3d(0, 0, 0);
    perspective: 1000;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

    position: fixed;
    top:0;
    left:0;
    right: 0;
    height: max(10vh, 40px);
    width: 200vw;
    background:var(--accent);
    -webkit-filter:url("#blob");
    filter:url("#blob");
}

@-webkit-keyframes bubble-size {
    0%, 75% {
        width:4rem;
        width:var(--size, 4rem);
        height:4rem;
        height:var(--size, 4rem);
    }
    100% {
        width:0rem;
        height:0rem;
    }
}

@keyframes bubble-size {
    0%, 75% {
        width:4rem;
        width:var(--size, 4rem);
        height:4rem;
        height:var(--size, 4rem);
    }
    100% {
        width:0rem;
        height:0rem;
    }
}

@-webkit-keyframes bubble-move {
    0% {
        top:-20rem;
    }
    100% {
        top:10rem;
        top:var(--distance, 10rem);
    }
}

@keyframes bubble-move {
    0% {
        top:-20rem;
    }
    100% {
        top:10rem;
        top:var(--distance, 10rem);
    }
}
